*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
body{
    background-color:#6b588a;
    color:white;
    font-family: 'Quicksand', sans-serif;
}

.container{
    min-height: calc(100vh-50px);
    display:flex;
    flex-direction: column;
    flex-basis: 95%;
    justify-content: space-between;
    margin: 1%;
}

.tweets{
    flex:0 0 90%;
}

.profile{
    flex:0 0 90%;
    order:-1;
    margin: 1%;
}


.footer{
    text-align: center;
    width:100%;
    height:50px;
    background-color: #3c3232;
    padding:2%;
    color:white;
}
/* for desktops*/
@media (min-width:801px) {
    .container{
        /* flex:0 0 98%; */
        flex-direction: row;
        /* justify-content: space-between; */
    } 
    .tweets{
        flex:0 0 45%;
    }
    .profile{
        flex:0 0 45%;
        order:2;
        margin: 1%;
    }
}