.tweet{
    background-color: #dccdcd;
    padding: 5px;
    margin:2%;
    color:black;
    flex:0 0 80%;
    flex-flow: column wrap;
    justify-content: space-between;
}
.tweet p, .tweet span{
    flex:0 0  5%;
}
.retweets,.comments{
    margin-left:2%;
}
.tweet img{
    border-radius: 100%;
    height:50%;
}
.new-tweet{
    display:flex;
    background-color: black;
    flex-direction: column wrap;
}
.tweet-content{
    flex-basis: 50%;
}